import * as React from "react";
import { useNavigate } from "react-router-dom";
import { BottomNavigation, BottomNavigationAction } from "@mui/material";
import SentimentSatisfiedAltIcon from "@mui/icons-material/SentimentSatisfiedAlt";
import HomeIcon from "@mui/icons-material/Home";
import CalendarMonthIcon from "@mui/icons-material/CalendarMonth";
import ArticleIcon from "@mui/icons-material/Article";
import { Box } from "@mui/material";
/* import AddIcon from '@mui/icons-material/Add'; */
import AddCircleIcon from '@mui/icons-material/AddCircle';
export default function LabelBottomNavigation() {

  const [value, setValue] = React.useState("recents");
  const nav = useNavigate();
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  /* if (window.location.pathname === '/') return null; */

  return (
    <div>
        {/* position:'fixed' :      >  content  content    ex)timeline >  z-index   > timeline footer  z-index              > timeline    css    >  mb()   timeline       > fixed           > mt     */}
        <Box sx={{ mb: "0px" }}>
          <BottomNavigation
            sx={{ position: "fixed", width: "100%", bottom: '0px' }}
            value={value}
            showLabels
            onChange={handleChange}
          >
  
            <BottomNavigationAction
              onClick={() => {
                nav("/");
              }}
              label=""
              value="home"
              icon={<HomeIcon />}
            />

            <BottomNavigationAction
              onClick={() => {
                nav("/calendar");
              }}
              label=""
              value="calendar"
              icon={<CalendarMonthIcon />}
            />

              <BottomNavigationAction sx={{ color:'#CEDD38'}}
              /* <BottomNavigationAction sx={{ color:'red' ,backgroundColor:'white' }} */
            onClick={()=>{nav("/addEvent")}}
            label=""
            value="Add"
            icon={<AddCircleIcon />}
            />  

            <BottomNavigationAction
              onClick={() => {
                nav("/memo");
              }}
              label=""
              value="memo"
              icon={<ArticleIcon />}
            />

            <BottomNavigationAction
            
              onClick={() => {
                nav("/myProfile");
              }}
              label=" "
              value="myProfile"
              icon={<SentimentSatisfiedAltIcon />}
            />
          </BottomNavigation>
        </Box>
    </div>
  );
}